<template>
  <div class="admin-sign login">
      <div class="login-box" v-if="!code">
          <div class="input-box">
              <div class="sign-box">
                <p @click="signInCh">
                    SIGN IN
                </p>
                <p @click="signUpCh">
                    SIGN UP
                </p>
              </div>
            
            <input type="text" placeholder="ID" v-model="id">
            <input type="text" placeholder="EMAIL" v-model="email" v-if="signUp">
            <input type="password" name="" id="" placeholder="PASSWORD" v-model="psw">
            <input type="password" name="" id="" placeholder="PASSWORD CHK" v-if="signUp" v-model="pswchk">
            <p v-if="signUp && psw != pswchk" class="wrong">
                다른 비밀번호입니다.
            </p>
            <button v-if="signUp == false" @click="signChk">
              SIGN IN
            </button>
            <button v-if="signUp" @click="signUpchk">
                SIGN UP
            </button>
          </div>


          <div class="portal-login">
              <ul>
                  <li>카카오</li>
                  <li>네이버</li>
                  <li>구글</li>
              </ul>
          </div>

          <div class="find-sign-in" v-if="signUp == false">
              <ul>
                  <li>
                      <a href="#">
                          아이디 찾기
                      </a>
                  </li>
                  <li>
                      <a href="">
                          비밀번호 찾기
                      </a>
                  </li>
              </ul>
          </div>
      </div>
      <!-- <div class="code-box" v-if="code">
          <div class="input-box">
            <p>
                EMAIL CODE
            </p>
            <p class="code-nav">
                메일로 받은 코드를 입력 해 주세요
            </p>
            <p class="code-nav">
                {{email}}
            </p>
            <input type="text" placeholder="CODE" v-model="codeInput">
            <button @click="signUpFin">
                SIGN UP
            </button>
          </div>
      </div> -->
  </div>
</template>

<script>
export default {
    // name:'Login',
    data() {
        return {
            signUp: false,
            // code: false,

            id: '',
            email: '',
            psw: '',
            pswchk: '',
            codeInput:'',
        }
    },
    watch:{
        pswchk(){
            if(this.signUp){
                if(this.psw != this.pswchk){
                    console.log('다른 비밀번호');
                }
            }
        }
    },
    mounted() {
        document.body.classList.add('bg-lightGray')
    },
    unmounted() {
        document.body.classList.remove('bg-lightGray')
    },


    methods: {
        
        signInCh(){
            if(this.signUp){
                this.signUp = false
                this.id = ''
                this.email = ''
                this.psw = ''
                this.pswchk = ''
            }
        },

        signUpCh(){
            if(!this.signUp){
                this.signUp = true
                this.id = ''
                this.email = ''
                this.psw = ''
                this.pswchk = ''
            }
        },

        signChk(){
            // axios.post('http://ec2-3-36-180-201.ap-northeast-2.compute.amazonaws.com:8088/user/login')
            // .then((res) => {
            //     console.log(res.data);
            // })
            if(this.id == '' && this.psw == ''){
                console.log('빈 값');
                this.signUp = true;
            }
            if(this.id != '' && this.psw == ''){
                console.log('비밀번호 입력');
            }
            if(this.id == '' && this.psw != ''){
                console.log('아이디 입력');
            }

            if(this.id != ''  && this.psw != ''){
                // if(this.validEmail(this.id)){
                    console.log('로그인');

                    this.$router.push({name: 'Admin' , path:'mng'})
                // }else if(!this.validEmail(this.id)){
                //     console.log('아이디 형식 확인');
                // }
            }
        },
        signUpchk(){
            if(this.email != '' && this.psw !='' && this.pswchk != ''){
                if(this.validEmail(this.email)){
                    // if(this.psw == this.pswchk){

                    //     this.code = true
                    // }
                    // else if(this.psw != this.pswchk){
                    //     this.code = false
                    // }
                    
                    // 메일 인증
                }
                else if(!this.validEmail(this.email)){
                    console.log('이메일 형식 오류');
                }
            }
            
            if(this.email == ''){
                console.log('이메일 입력');
            }
            if(this.psw == ''){
                console.log('비밀번호 입력');
            }
            if(this.pswchk == ''){
                console.log('비밀번호 확인');
            }
        },

        validEmail(email){
            var re = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;

            return re.test(email);
        }
    },
}
</script>

<style lang="scss">
@import '@/assets/css/signIn/signIn.scss'
</style>